.portfolio-container {
  width: 480px;
  height: 1784px;
  padding: 15px 0px 15px 0px;
  margin: 0px;
  background-color: tomato;
}

.portfolio-span {
  display: inline;
}

.portfolio-box > li:nth-child(-n + 3) {
  margin: 30px;
}

.portfolio-box > li {
  text-decoration: none;
}

.portfolio-box {
  display: block;
}

.portfolio-Razor,
.portfolio-towe,
.portfolio-shavin,
.portfolio-heirbrush {
  background-size: cover;
  background-repeat: no-repeat center;
}

.small-text {
  width: 338px;
  height: 42px;
  font-family: Open Sans;
  font-style: normal;
  font-weight: 600;
  font-size: 11px;
  line-height: 21px;
  /* or 191% */
  letter-spacing: 0.1em;
  text-transform: uppercase;
  fill: #303030;
}
