.contacts-section {
  background-color: #191c26;
  font-family: Raleway, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell,
    'Open Sans', 'Helvetica Neue', sans-serif;
}

.contacts-title {
  font-family: Raleway;
  font-style: normal;
  font-weight: bold;
  font-size: 42px;
  line-height: 49px;
  letter-spacing: 0.05em;
  color: #ffffff;
  text-align: center;
}
.form-field {
  margin-top: 44px;
  padding: 0px 0px;
  width: 100%;
  border-bottom: 1px solid rgba(157, 164, 189, 0.4);
  font-family: Open Sans;
  font-style: normal;
  font-weight: 600;
  font-size: 11px;
  line-height: 15px;
  letter-spacing: 0.1em;
  color: rgba(157, 164, 189, 0.4);
}
.form-input {
  width: 100%;
  border: none;
  background-color: inherit;
  height: 24px;
  color: rgba(157, 164, 189, 0.4);
}
.textarea-input {
  height: 76px;
  width: 100%;
  border: none;
  background-color: inherit;
  color: rgba(157, 164, 189, 0.4);
}

.knock {
  font-family: Open Sans;
  font-style: normal;
  font-weight: 600;
  font-size: 12px;
  line-height: 16px;
  text-align: center;
  letter-spacing: 0.09em;
  display: block;
  margin-top: 44px;
  padding: 12px 38px;

  margin-left: auto;
  margin-right: auto;
  border: 1px solid #ff6c00;
  border-radius: 25px;
  color: #ffffff;
  background-color: inherit;

  /* justify-content: center;
    align-items: center; */
}
.contacts-background {
  background-image: linear-gradient(rgba(0, 0, 0, 0.95), rgba(0, 0, 0, 0.9)),
    url(../images/contacts-backgroundX1.jpg);
  background-size: cover;
  background-position: center;
  /* opacity: 0, 1; */
}
.contacts-text {
  font-family: Open Sans;
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 30px;
  letter-spacing: 0.02em;
  /* text-decoration-line: underline; */
  color: #9da4bd;
  text-decoration: none;
}
.icon-contacts {
  margin-right: 16px;
  padding: 1px;
}
.working-hours {
  margin-top: 44px;
}
